.contacts-section {
  background-color: #84a178;
  background-image: url("Backgroud_mobile.cf01bccf.jpg");
  background-position: 50% -885px;
  background-repeat: repeat-x;
}

.contacts {
  flex-direction: column;
  align-items: center;
  padding-bottom: 60px;
  display: flex;
}

.contacts__title {
  width: 0;
  height: 0;
  opacity: 0;
}

.contacts__buttons {
  flex-direction: column;
  display: flex;
}

.contacts__btn-locations {
  width: 164px;
  height: 42px;
  color: #000;
  text-align: center;
  cursor: pointer;
  width: 196px;
  background-color: #f8d24e;
  border: none;
  border-radius: 10px;
  margin-top: 24px;
  margin-bottom: 16px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.3;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), color .25s cubic-bezier(.4, 0, .2, 1);
}

.contacts__btn-locations:hover {
  background-color: #0a8817;
}

.contacts__btn-locations:focus {
  color: #fff;
  background-color: #0a8817;
}

.contacts__btn-locations:disabled {
  background-color: #ababab;
}

.contacts__btn-franchise {
  width: 164px;
  height: 42px;
  color: #000;
  text-align: center;
  cursor: pointer;
  width: 196px;
  background-color: #0000;
  border: 2px solid #f8d24e;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.3;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), border .25s cubic-bezier(.4, 0, .2, 1);
}

.contacts__btn-franchise:hover {
  border-color: #0a8817;
}

.contacts__btn-franchise:focus {
  color: #fff;
  border-color: #0a8817;
}

.contacts__btn-franchise:disabled {
  border-color: #ababab;
}

.contact {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.contact__item {
  max-width: 280px;
